:root{
    --PrimaryColor:hsl(26,93%,50%);
    --HoverColor:hsl(26,90%,42%);
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(0,0%,18%);
    --textColor:hsl(240,1%,48%);
    --whiteColorDeam:hsl(0,0%,93%);
    --greyBg:hsl(0,0%,96%);
    --greyText:rgb(190,190,190);
    --inputColor:hsl(330,12%,97%);
}

.navBarSection{
    .header{

        position: fixed;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: transparent;
        width: 100%;
        padding: 1rem;
        z-index: 1000;
    


      .logoDiv{
            .logo{
                color: var(--whiteColor);
                font-weight: 500;
                cursor: pointer;
                align-items: center;
                justify-content: center;

                .icon{
                    color: var(--PrimaryColor);
                    font-size: 25px;
                }
                


            }
 
        }

        @media screen and (max-width: 767px){
            .navBar{
                background: var(--greyBg) ;
                position: absolute;
                height: max-content;
                padding: 1rem;
                width: 100%;
                top: 0;
                left: -500%;
                box-shadow: rgba(209,209,209,.904);
                transition: .5s ease;
                z-index: 1000;


                .navLists{
                    flex-direction: column;
                    justify-content: center;
                    width: 100%;
                    margin: auto;

                    .navItem{
                        padding: 1rem;
                    }
                

                


            .headerBtns{
                flex-direction: column;

                .btn{
                    margin-top: 1rem;

                    a{
                        font-weight: 600;

                        &:hover{
                            color: var(--whiteColor);

                        }
                    }

                }

                .loginBtn{
                    background: none;
                    border: 1px solid var(--PrimaryColor);
                    
                    a{
                        color: var(--PrimaryColor);
                    }

                    &:hover{
                        background:  var(--HoverColor
                        );
                        color: var(--whiteColor
                        );

                        a{
                            color: var(--whiteColor);
                        }
                    }

                }
            }
                
                }
             .closeNavbar{
                position: absolute;
                top: 1rem;
                right: 1.5rem;
                color:var(--PrimaryColor);
                & :hover .icon{
                    color: var(--HoverColor);
                }

                


             }
             
            }
 
            .activeNavbar{
                left: 0;
            }

        }

        .toggleNavbar{
            font-size: 25px;
            color: var(--whiteColor);
            &:hover{

                color: var(--PrimaryColor);

            }
        }
    }

    .activeHeader{
        background: var(--textColor);
        box-sizing: 0 2px 8px 2px rgba(178,178,178,.45);

    }


}



@media screen and (min-width:760px) {

    .toggleNavbar,.closeNavbar{
        display: none;
    }
    .header{
        padding: 1.5rem 2rem;
        .navBar{
            .navLists{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: row;
                .navItem{
                    .navLink{
                        padding: 1rem;
                        color: var(--whiteColor);
                        font-weight: 500;
                        font-size: 14px;
                        cursor: pointer;
                        &:hover{
                            color: var(--PrimaryColor);
                        }
                         
                    }
                }
                .btn{
                    margin-left: 1rem;
                    a{
                        font-weight: 500;
                       
                    }
                }
                .loginBtn{
                    background: none;
                    border: 1px solid var(--PrimaryColor);
                    padding: .5rem 1rem;
                    a{
                        color: var(--PrimaryColor);
                    }

                    &:hover{
                        background:  var(--HoverColor
                        );
                        color: var(--whiteColor
                        );

                        a{
                            color: var(--whiteColor);
                        }
                    }

                }
            }
        }
    }
    
}