:root{
    --PrimaryColor:hsl(26,93%,50%);
    --HoverColor:hsl(26,90%,42%);
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(0,0%,18%);
    --textColor:hsl(240,1%,48%);
    --whiteColorDeam:hsl(0,0%,93%);
    --greyBg:hsl(0,0%,96%);
    --greyText:rgb(190,190,190);
    --inputColor:hsl(330,12%,97%);
}



.home{
    // padding-top:  4rem;
    height: 70vh;
    background: linear-gradient(rgba(33,33,33,0.522),rgba(33,33,33,0.5222)),url('../../Assets/img13.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    position: relative;

    .setContainer{
        padding: 2rem;
        
        .homeText{
              text-align: center;
            

            .title{
                color: var(--whiteColor);
                font-size: 2rem;
                font-weight: 700;
                line-height: 2.5rem;
            }

            .subTitle{

                color: var(--whiteColor);
                opacity: .9;
                font-size: 13px;
                font-weight: 300;
                padding: 1rem 0;
                max-width: 70%;
                text-align: center;
                margin: auto;                
            }
            
             .btn{
                padding: .6rem 1.5rem;


             }


        }

        .homeCard{
            width: 80%;
            margin-top:  1rem;
            border-radius: 1rem;
            // padding-top: 2rem;
            padding: 2rem;
            background-color: var(--whiteColor);
            position: absolute;
            gap: 1rem;
            justify-content: center;
            bottom: -35%;
            transform: translate(-50%);
            left: 50%;
            box-shadow: 0 2px 8px 4px;
        
            .locationDiv,.distDiv,.priceDiv{
                width: 100%;
                 
                label{
                    display: block;
                    color: var(--textColor);
                    font-weight: 500;
                    font-size: 13px;
                    padding-bottom: .4rem;

                }
                input{
                    width: 100%;
                    background: var(--inputColor);
                    border-radius: 10px;
                    border: none;
                    padding: 10px;
                    font-size: 13px;
                    font-weight: 500;

                    &::placeholder{
                        font-size: 13px;
                        opacity: .5;
                        font-weight: 400;

                    }
                    &:focus{
                        outline: none;
                    }




                }
            }

            .btn{
              width: 100%;
              padding: .6rem 1.5rem;
              color: var(--whiteColor);
              font-weight: 600;

            }
            
        }
    }


}

